body {
  background-color: rgb(66, 174, 224);
}
* {
  box-sizing: border-box;
  font-family: sans-serif;
  margin: 0;
  padding: 0;
}
.header {
  width: 100%;
  text-align: center;
  font-size: 30px;
  color: rgb(25, 32, 32);
  margin-top: 30px;
}
.theline {
  width: 100px;
  margin: auto;
  text-align: center;
  height: 10px;
  background-color: rgba(125, 125, 216, 0.788);
  position: relative;
  border-radius: 5px;
  overflow: hidden;
}

.animationdot {
  position: absolute;
  color: white;
  font-size: 20px;
  top: 2px;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background-color: white;
  left: 0px;
  animation: dotMove 5s infinite;
  transition: ease-in-out;
}
@keyframes dotMove {
  0% {
    left: 0px;
  }
  100% {
    left: 101px;
  }
}

.loading {
  margin-top: 40vh;
  font-weight: bold;
  font-size: 50px;
}

.container {
  max-width: 700px;
  background-color: inherit;
  margin: auto;
}
.container2 {
  text-align: center;
  line-height: 100px;
}
.tour {
  margin: 20px;
  background-color: white;
  border-radius: 5px;
  box-shadow: lightgray 5px 5px 10px;
}
.imageContainer {
  width: 100%;
  /* height: 20rem; */
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  overflow: hidden;
}
.imageContainer img {
  width: 100%;
  height: 20rem;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  object-fit: cover;
  animation: scaling 10s infinite;
}

@keyframes scaling {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.4);
  }

  100% {
    transform: scale(1);
  }
}

.details {
  padding: 15px;
  display: flex;
  justify-content: space-between;
}

.price {
  float: right;
  position: relative;
  background-color: rgba(92, 201, 190, 0.288);
  color: rgb(17, 121, 110);
  padding: 5px;
  /* font-size: 18px; */
  border-radius: 5px;
}

.info {
  padding: 15px;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  color: rgb(116, 110, 110);
  line-height: 23px;
  font-weight: 400;
}

.buttondiv {
  width: 100%;
  padding: 15px;
  text-align: center;
}

.btn {
  width: 30%;
  margin: auto;
  color: rgba(236, 104, 104, 0.856);
  padding: 10px;
  font-size: 18px;
  letter-spacing: 2px;
  border: rgba(236, 104, 104, 0.856) solid 2px;
  background-color: inherit;
  border-radius: 5px;
  /* border-width: transparent; */
  /* border-color: transparent; */
}

.btn-2 {
  color: rgba(236, 104, 104, 0.856);
  border-width: transparent;
  border-color: transparent;
  background-color: inherit;
}

.btn:hover {
  background-color: rgba(236, 104, 104, 0.856);
  color: white;
}

@media only screen and (max-width: 550px) {
  .btn {
    width: 50%;
  }
}

@media only screen and (max-width: 365px) {
  .btn {
    width: 80%;
  }
}
